.isomertic-shape {
    position: fixed;
    width: 100%;
    bottom: -30px;
    transform: rotate(180deg);
    z-index: 100;
}

.center-form {
    display: inline-block;
    width: 50%;
    position: absolute;
    font-size: 13px;
    padding: 0;
    margin-left: 50%;

    .logo {
        width: 60px;
        margin: 10px 10px;
        display: block;
    }

    .center-form-inner {
        height: 120px;
        display: block;
        margin: auto auto;
        padding: 60px;

        &.sign-up {
            margin: 10px auto 0;
        }

        .welcome-message {
            color: grey;
            margin: 0;
            margin-bottom: 15px;
            font-weight: 400;
            font-size: 14px;
        }

        button {
            height: 40px;
        }
    }
    md-input-container {
        margin-top: 10px;

        input {
            color: #656a72 !important;
        }
    }
}

.action-link {
    position: relative;
    right: 15px;

    a {
        font-weight: bold;
        padding-bottom: 3px;
        border-bottom: 1px solid #fff;
    }
}

.password-validation-message {
    margin-top: -10px;

    md-list {
        padding: 0;
        padding-bottom: 20px;
        .md-list-item-inner {
            padding: 0;
        }
    }

    p {
        margin: 0 !important;
        margin-bottom: 10px !important;
    }

    md-icon {
        padding: 0;
        margin: 0 !important;
        margin-right: 15px !important;
        margin-bottom: 10px !important;
        &.active {
            color: green;
        }
    }
}

.visibilityButton {
    position: relative;
    margin-top: -85px !important;
    margin-left: 90% !important;
}

.forgot-password-link {
    margin-top: -8px !important;
    margin-bottom: -8px;
}

.certificate {
    margin-top: 0;
    margin-bottom: -30px;
    margin-left: auto;
    margin-right: auto;
    display: inline-block;

    img {
        width: 90px !important;
        display: block !important;
        height: 54px;
    }
}

.password-validation {
    &.color {
        color: #007BFF;
    }

    &.header {
        margin-top: 0;
    }
}

.checkmark-circle-active {
    color: red;
}
