.contacts-wrap {
    margin: 0;
    padding: 15px 0;

    md-table-container {
        table {
            border-spacing: 0 10px;
            border-collapse: collapse;

            thead {
                tr {
                    th {
                        md-menu {
                            button {
                                md-icon {
                                    margin-right: 0 !important;
                                }
                            }
                        }
                    }
                }
            }

            tbody {
                tr {
                    border-bottom: 2px solid #fbfbfb;
                    transition: .2s;

                    &:hover {
                        border-color: #007bff;
                    }

                    td {
                        a {
                            padding: 5px 0;
                        }
                    }
                }
            }
        }
    }
}

.title-rule {
    margin-top: 10px;
}

.column-info {
    background: #f6f6f6;
    overflow-y: auto;

    .info-item {
        background: #f6f6f6;
