}

.list-guest {
    .guest-item {
        border: 1px solid #ffd088;
        border-radius: 5px;
        box-sizing: border-box;
        cursor: pointer;
        padding: 11px;
        height: 107px;
        position: relative;
        margin: 0 0 7px;
        transition: .3s;
        overflow: hidden;

        &:hover {
            border-color: #286efa;
            background-color: rgba(0, 0, 0, .3);
            height: 137px;

            .guest-link, .associate {
                display: block;
                opacity: 1;
            }
        }
    }

    .guest-link {
        border: 1px solid transparent;
        border-radius: 4px;
        display: none;
        color: #fff;
        position: absolute;
        right: 15px;
        bottom: 7px;
        transition: .2s;
        padding: 3px 8px 6px;
        z-index: 2;
        opacity: 0;
        width: 70px;
        text-align: center;

        &:hover {
            border-color: #fff;
        }
    }

    .associate {
        border: 1px solid transparent;
        border-radius: 4px;
        color: #fff;
        margin: 0;
        padding: 6px 8px;
        position: absolute;
        z-index: 2;
        left: 15px;
        transition: .2s;
        min-width: auto;
        line-height: 1;
        opacity: 0;
        bottom: 7px;

        &:hover {
            border-color: #fff;
        }
    }

    ul {
        margin: 0;

        li {
            padding: 0;
            margin: 0 0 5px;
        }
    }

    .guest-title {
        display: inline-block;
        width: 30%;
    }
}

