.message-list {
    padding: 0;
    margin: 0;
    overflow-x: auto;
    user-select: none;
    height: 100%;
    max-height: 100%;

    md-list {
        padding: 0;
    }

    .md-tab {
        padding: 12px 15px;
    }

    .cp-Panel-toggle {
        margin: 0;
        padding: 10px 15px 8px;
        text-transform: capitalize;

        &.cp-is-open {
            background-color: #f3f6f8 !important;
            font-weight: 600;
        }
    }

    .badge {
        background-color: transparent;
        padding: 3px 7px;
        color: #656a72;
        border: none;
        border-radius: 30px;
        float: right;
        height: 12px;
        line-height: 12px;
        margin-top: 8px;
    }

    .message-item {
        padding: 5px 10px;
        transition: background-color .3s;

        .badge {
            font-size: 12px;
            margin-top: 15px;
        }

        .md-list-item-inner {
            padding: 0;
            width: 100%;
        }

        .message-link {
            box-sizing: border-box;
            padding: 10px;
            border-top-left-radius: 6px;
            border-top-right-radius: 6px;
            cursor: pointer;
            font-size: 16px;
            transition: background ease .5s;
            width: 100%;
            border-bottom: 2px solid #EAEAEA;

            &:hover {
                background-color: #f5f5f5;
            }

            &.unread {
                -webkit-animation: blinker 1s linear 1s infinite alternate;
                animation: blinker 1s linear 1s infinite alternate;
            }

            .star-icon-message {
                position: absolute;
                right: 30px;
                top: 0;

                &:hover {
                    md-icon {
                        color: #ffbb3e;
                        display: block;
                    }
                }
            }

            md-icon {
                &.active {
                    color: #ffbb3e;
                    display: block;
                }
            }

            .md-avatar {
                margin: 5px;
                float: left;
                padding: 5px 0;
            }

            .message-text-bottom {
                display: flex;
                align-items: center;
                width: 100%;
                margin-top: 4px;
            }

            .md-list-item-text {
                .message-text, .message-time {
                    width: 50%;
                    overflow-x: hidden;
                    padding-right: 5px;
                    text-overflow: ellipsis;
                    white-space: nowrap;
                }

                .message-time {
                    padding-right: 0;
                    font-size: 14px;
                }

                h3 {
                    padding-right: 54px;
                }

                h3, .message-text, .message-time {
                    color: #96a3af;
                }
            }

            &.selected {
                background-color: #f5f5f5;

                .md-list-item-text {
                    h3, .message-text, .message-time {
                        color: #606a73;
                    }
                }
            }
        }

        .empty {
            height: 63px;

            &.message-link:hover {
                background-color: transparent !important;
                cursor: auto !important;
            }
        }
    }
}

