.transition-loader {
    background-color: rgba(0, 0, 0, .75);
    position: absolute;
    top: 0;
    color: #fff;
    left: 0;
    height: 100%;
    text-align: center;
    width: 100%;
    z-index: 2;

    .transition-loader-text {
        font-size: 16px;
        top: 50%;
        position: absolute;
        margin: auto;
        left: 0;
        right: 0;
    }
}

.transition-loader-inner {
    top: 60%;
    position: absolute;
    padding: 0 100px;
    left: 0;
    right: 0;

    label {
        border-radius: 50%;
        margin: 0 10px;
        opacity: 0;
        padding: 0;
        display: inline-block;
        background: #fff;
        width: 10px;
        height: 10px;
    }
}

$key-frames-ms: 600ms, 500ms, 400ms, 300ms, 200ms, 100ms;

@each $current-ms in $key-frames-ms {
    $i: index($key-frames-ms, $current-ms);
    .transition-loader-inner label:nth-child(#{$i}) {
        -webkit-animation: loader 3s $current-ms infinite ease-in-out;
        animation: loader 3s $current-ms infinite ease-in-out;
    }
}

@keyframes loader {
    0% {
        opacity: 0;
        transform: translateX(-300px) scale(1);
    }
    33% {
        opacity: 1;
        transform: translateX(0px) scale(1.5);
    }
    66% {
        opacity: 1;
        transform: translateX(0px) scale(1);
    }
    100% {
        opacity: 0;
        transform: translateX(300px) scale(1.5);
    }
}

@-webkit-keyframes loader {
    0% {
        opacity: 0;
        -webkit-transform: translateX(-300px);
    }
    33% {
        opacity: 1;
        -webkit-transform: translateX(0px);
    }
    66% {
        opacity: 1;
        -webkit-transform: translateX(0px);
    }
    100% {
        opacity: 0;
        -webkit-transform: translateX(300px);
    }
}

