.flow-block {
    cursor: pointer;
    box-sizing: border-box;
    border-bottom: 2px solid #f6f6f6;
    transition-duration: 400ms;
    transition-property: border-bottom, background-color;
    display: inline-block;
    padding: 0 10px;
