    }

    .select-user {
        width: 70%;
    }

    .selected-list {
        .item {
            display: inline-block;
            cursor: pointer;
        }
    }
}

.escalation-details .content,
.escalation-create-sidebar,
.task-details .content {
    .cp-Panel {
        margin-bottom: 20px;

        .cp-Panel-toggle {
            background-color: #f5f5f5;
            border-radius: 10px;
            color: $TextGreyColor;
            display: flex;
            padding: 4px 13px 3px;
            font-size: inherit;

            .title-icon {
                fill: #989A9F;
                width: 18px;
                height: 18px;
                margin-right: 12px;
            }

            .arrow-icon {
                transition: .2s;
                transform: rotate(90deg);
                height: 16px;
                width: 16px;
                position: absolute;
                right: 13px;
                top: 13px;
            }

            &.cp-is-open {
                border-radius: 10px 10px 0 0;

                .arrow-icon {
                    transform: rotate(0deg);
                }
            }
        }

        .cp-Panel-body {
            border-radius: 0 0 10px 10px;
            padding: 13px;

            &.cp-is-open {
                border: solid 1px #F5F5F5;
                border-top: none;
            }
        }

        .label {
            margin-bottom: 5px;
        }
    }
}

.section-conditions,
.section-reminders,
.section-escalations {
    .condition,
    .reminder,
    .escalation {
        border: solid 1px #B0DCF7;
        border-radius: 15px 15px 10px 10px;
        background-color: #EFFAFF;

        .delete-btn {
            cursor: pointer;
        }

        table {
            width: 100%;
            padding: 10px;

            td {
                padding: 0 5px;
            }
        }

        .block-cond {
            padding: 10px 15px 5px 15px;
        }

        .block-values {
            padding: 5px 15px 15px 15px;
        }

        .separator {
            position: relative;
            margin: 10px 0;
            border: solid 1px #B0DCF7;

            &:before {
                content: "AND";
                position: absolute;
                top: -10px;
                left: 50%;
                background-color: #fff;
                border: solid 1px #B0DCF7;
                border-radius: 10px;
                height: 20px;
                padding: 0 10px;
            }

            &:nth-last-child(2) {
                display: none;
            }
        }

        .selected-list {
            padding: 2px;

            .item:hover {
                background-color: #fff;
            }
        }

        .block-footer {
            background-color: #fff;
            border-radius: 0 0 10px 10px;
            color: #3f69cc;

            span {
                display: inline-block;
                cursor: pointer;
                height: 20px;
                line-height: 20px;
                margin: 10px 15px;

                md-icon {
                    color: #3f69cc;
                }
            }
        }
    }

    .title-section {
        margin: 15px 0;
    }
}

.section-slatarget {
    overflow-x: auto;

    span {
        font-weight: 600;
    }

    .urgent {
        color: #CF2A27;
    }

    .high {
        color: #FF7629;
    }

    .medium {
        color: #FCC024;
    }

    .low {
        color: #44AE5F;
    }

    .table {
        tbody tr {
            border: solid 2px #eee;
        }

        td {
            max-width: none;
            padding: 0 15px;
        }
    }
}

.name-contact-table {
    min-width: 200px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;

    .mini-avatar {
        margin-right: 5px;
        vertical-align: middle;
    }

    .link {
        vertical-align: middle;
    }
}

.field-creation {
    .fields {
        border: solid 1px #f5f5f5;

        .list {
            padding: 5px;

            .item {
                border: solid 1px #dadada;
                margin: 5px;

                &.blur {
                    > .field-header {
                        opacity: .3;
                    }

                    .field-title {
                        cursor: default !important;
                    }

                    .expand-sections,
                    .actions {
                        display: none !important;
                    }
                }

                .group-subfield-list {
                    .field-header {
                        .drag-indicator {
                            display: none;
                        }
                    }
                }

                .field-header {
                    min-height: 40px;

                    .drag-indicator {
                        display: flex;
                        align-items: center;

                        .svg-icon {
                            width: 33px;
                            height: 24px;
                        }
                    }

                    &:hover {
                        .actions {
                            display: block;
                        }
                    }

                    .field-title {
                        cursor: pointer;
                        flex: 20;
                        padding: 5px;

                        .label {
                            font-weight: bold;
                        }

                        .system,
                        .required {
                            background-color: #d4d4d4;
                            display: inline-block;
                            border-radius: 4px;
                            padding: 5px 15px;
                            margin-left: 10px;
                        }

                        .system {
                            color: #4caf50;
                        }
                    }

                    .actions {
                        display: none;
                    }
                }

                .details {
                    .choices {
                        .drag_handle {
                            line-height: 30px;
                        }

                        svg {
                            width: 22px;
                            height: 22px;
                            vertical-align: bottom;
                        }
                    }

                    .field-form {
                        background-color: #f9f9f9;
                        padding: 5px;

                        input, textarea {
                            background-color: #fff;
                            border: solid 1px #d4d4d4;
                        }
                    }

                    .edit-actions {
                        text-align: right;
                        background-color: #fff;

                        .primary-button,
                        .secondary-2-button {
                            margin: 18px 10px 18px 0;
                        }
                    }
                }

                .sections {
                    border-top: solid 2px #d4d4d4;
                    background-color: #f9f9f9;

                    .expand-sections {
                        cursor: pointer;
                    }

                    .section {
                        border: solid 1px #b3bec6;
                        background-color: #fff;

                        .details {
                            .section-form {
                                background-color: #f9f9f9;
                                padding: 5px;

                                .selected-list {
                                    padding: 2px;

                                    .item {
                                        cursor: pointer;
                                        white-space: nowrap;
                                        display: inline-block;
                                    }

                                    .item:hover {
                                        background-color: #fff;
                                    }
                                }

                                input {
                                    background-color: #fff;
                                    border: solid 1px #d4d4d4;
                                }

                                md-input-container {
                                    margin-top: 10px !important;
                                }
                            }
                        }

                        .empty-list {
                            text-align: center;
                        }

                        .section-header {
                            background-color: #ebeff3;
                            min-height: 40px;

                            &:hover {
                                .actions {
                                    display: block;
                                }
                            }

                            &.blur {
                                opacity: .3;

                                .actions {
                                    display: none !important;
                                }
                            }

                            .section-title {
                                flex: 20;
                                padding: 10px;

                                b:last-child {
                                    display: none;
                                }
                            }

                            .actions {
                                display: none;
                            }
                        }
                    }
                }
            }
        }
    }

    .type-icon {
        position: relative;
        max-width: 45px;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 30%;

        div {
            border-radius: 5px;
            width: 32px;
            height: 32px;
            display: flex;
            justify-content: center;
            align-items: center;

            .svg-icon {
                width: 24px;
                height: 24px;
            }
        }
    }

    .field-types {
        background-color: #f9f9f9;

        .header {
            padding: 10px;
        }

        &.blur {
            opacity: .3;

            .item {
                cursor: default !important;
            }

            .actions {
                display: none !important;
            }
        }

        .types-list {
            .item {
                position: relative;
                background-color: #fff;
                border: solid 2px #d4d4d4;
                margin: 5px;
                cursor: pointer;

                .type-title {
                    flex: 20;

                    .label {
                        color: #606a73;
                        padding: 2px;
                    }

                    .description {
                        color: #d4d4d4;
                        padding: 2px;
                    }
                }

                .actions {
                    position: absolute;
                    right: 0;
                    top: 0;
                    background-color: #fff;
                    display: none;
                }

                &:hover {
                    .actions {
                        display: block;
                    }
                }
            }

        }
    }

    .-placeholderAfter {
        padding-bottom: 13px;
    }

    .-placeholderBefore {
        padding-top: 13px;
    }

    .-isEmpty {
        padding-bottom: 40px;
    }

    .-isEmpty:before,
    .-placeholderBefore:before,
    .-placeholderAfter:before {
        background-color: #a0a0a0;
        height: 3px;
        width: 100%;
        content: ' ' !important;
        margin-top: 5px !important;
        margin-bottom: 5px !important;
    }
}

.popup.max-large {
    width: 70%;
    min-width: 70%;
    max-width: 70%;
}

.merge-guest-popup {
    background: #f5f5f5;
    display: flex;

    .row-merge {
        flex: 1;

        > div {
            border-bottom: 1px solid rgba(0, 0, 0, .15);
            padding: 10px;

            &:last-child {
                border: none;
            }
        }

        &:nth-child(2), &:nth-child(3) {
            > div {
                color: #1E88E5;
                cursor: pointer;
                position: relative;

                &.active {
                    &:after {
                        position: absolute;
                        right: 10px;
                        top: 5px;
                        content: "\2714";
                    }
                }
            }
        }

        &:nth-child(2) .title-avatar {
            &:after {
                position: absolute;
                content: '\279C' !important;
                top: 30px !important;
                right: 5px !important;
                font-size: 16px;
            }
        }

        &:nth-child(3) .title-avatar {
            &:after {
                position: absolute;
                content: '\22B6' !important;
                top: 30px !important;
                right: -8px !important;
                font-size: 20px;
            }
        }

        &:nth-child(4) {
            background: #fff;
            pointer-events: none;
        }
    }

    .title-avatar {
        align-items: center;
        color: #656a72 !important;
        height: 60px;

        .md-avatar {
            margin-right: 10px;
        }

        b {
            font-size: 14px;
        }

        p {
            font-size: 11px;
            margin: 3px 0;
        }
    }
}

.pell-editor-wrap {
    background: #f5f5f5;
    color: #bdbac2;
    border-radius: 6px;
    height: 300px;

    .pell-actionbar {
        border: none;
    }
}

.no-messages {
    align-items: center;
    justify-content: center;
    display: flex;
    height: 100%;
    width: 100%;

    &._escalation, &._response {
        position: absolute;
    }
}

.inline-flex {
    display: inline-flex;
    align-items: center;
}

// tag-input component
.emberTagInput {
    margin: 0;
    padding: 5px 5px 2px 5px;
    border: 1px solid lightgray;
    cursor: text;
}

.emberTagInput--readOnly {
    cursor: default;
}

.emberTagInput li {
    list-style-type: none;
    display: inline-block;
}

.emberTagInput-tag,
.emberTagInput-input {
    font: 12px sans-serif;
    padding: 5px 10px;
    margin-bottom: 3px;
}

.emberTagInput-tag {
    background: cornflowerblue;
    border-radius: 20px;
    margin-right: 5px;
    color: white;
}

.emberTagInput-tag--remove {
    opacity: 0.75;
}

.emberTagInput-remove:before {
    content: 'x';
    cursor: pointer;
}

.emberTagInput-input {
    border: none;
    padding-left: 0;
}

.emberTagInput-input:focus {
    outline: none;
}

.emberTagInput-input.is-disabled {
    display: none;
}

.end-justify-align {
    display: flex;
    justify-content: flex-end;
}

.upper-text {
    text-transform: uppercase;
}