.svg-icons {
    position: absolute;
    padding: 15px 15px 15px 15px;
    &.validation-svg {
        padding: 0px 0px 0px 15px;
    }
    top: 0;
    bottom: 0;
    left: 0;
    line-height: 0;
    margin-bottom: 0;
    opacity: .65;
}

.entry-buttons {
    background-color: transparent;
    margin-bottom: -15px;
    font-size: 16px;
    color: #007BFF;

    &:disabled {
        background-color: transparent;
        opacity: .42;
        color: #a1b0bd;
    }
}

.btn span {
    position: relative;
    &:active {
        transition: 50ms;
        box-shadow: inset 2px 2px 5px 1px rgba(0, 0, 0, .15);
        width: 100%;
        border-radius: 6px;
        background-color: transparent;
        span {
            top: 1px;
        }
        &:focus, &:hover {
            box-shadow: 0 1px 1px 1px rgba(0, 0, 0, .2);
            transition: 50ms;
            outline: none;
            span {
                bottom: 1px;
            }
        }
    }
}

.grey-btn {
    display: inline-block;
    padding: 9px 0;
    width: 100%;
    background: #F9F9F9;
    font-weight: 600;
    line-height: 1.7;
    color: #007bff;
    text-align: center;
    border-radius: 6px;
    margin-top: 15px;
    border: none;
    -moz-transition: all 0.3s;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
    span {
        position: relative;
    }
    &:focus, &:hover {
        box-shadow: 0 1px 1px 1px rgba(0, 0, 0, .2);
        transition: 50ms;
        outline: none;
        span {
            bottom: 1px;
        }
    }
    &:active {
        transition: 50ms;
        box-shadow: inset 2px 2px 5px 1px rgba(0, 0, 0, .15);
        width: 100%;
        border-radius: 6px;
        background-color: #F9F9F9;
        span {
            top: 1px;
        }
    }
}

.white-btn {
    display: inline-block;
    padding: 9px 0;
    width: 100%;
    background: transparent;
    font-weight: 600;
    line-height: 1.7;
    color: #007bff;
    text-align: center;
    border-radius: 6px;
    margin-top: 15px;
    border: none;
    -moz-transition: all 0.3s;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
    box-shadow: 0px 1px 1px 1px rgba(0, 0, 0, .2);
    span {
        position: relative;
    }
    &:focus, &:hover {
        box-shadow: 0 1px 5px 1px rgba(0, 0, 0, .2);
        transition: 50ms;
        outline: none;
    }
    &:active {
        transition: 50ms;
        box-shadow: inset 2px 2px 5px 1px rgba(0, 0, 0, .15);
        width: 100%;
        border-radius: 6px;
        background-color: #F9F9F9;
        span {
            top: 1px;
        }
    }
}

.blue-btn {
    display: inline-block;
    padding: 9px 0;
    width: 100%;
    background: #007bff;
    font-weight: 600;
    line-height: 1.7;
    color: white;
    text-align: center;
    border-radius: 6px;
    margin-top: 15px;
    border: none;
    -moz-transition: all 0.3s;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
    span {
        position: relative;
    }
    &:focus, &:hover {
        box-shadow: 0px 1px 5px 1px rgba(0, 0, 0, .4);
        transition: 50ms;
        outline: none;
    }
    &:active {
        transition: 50ms;
        box-shadow: inset 2px 2px 5px 1px rgba(0, 0, 0, .15);
        width: 100%;
        border-radius: 6px;
        background-color: #006EE6;
        span {
            top: 1px;
        }
    }
    &:disabled {
        background-color: #a1b0bd;
        pointer-events: none;
    }
}
