// Components
textarea::-webkit-input-placeholder {
    color: #bdbac2;
}

textarea::-moz-placeholder {
    color: #bdbac2;
}

textarea:-ms-input-placeholder {
    color: #bdbac2;
}

textarea:-moz-placeholder {
    color: #bdbac2;
}

input::-webkit-input-placeholder {
    color: #bdbac2;
}

input::-moz-placeholder {
    color: #bdbac2;
}

input:-ms-input-placeholder {
    color: #bdbac2;
}

input:-moz-placeholder {
    color: #bdbac2;
}

.link {
    color: #1844ef !important;
    cursor: pointer;

    [class^="icon-"], [class*=" icon-"] {
        font-weight: 600;
    }

    &:hover {
        background-color: transparent !important;
        text-decoration: underline;

        [class^="icon-"], [class*=" icon-"] {
            color: #fff !important;
        }
    }
}

.two-field {
    justify-content: space-between;

    > div {
        width: 45%;
    }
}

md-radio-button.md-checked {
    .md-off {
        border-color: #3f69cc !important;
    }

    .md-on {
        background-color: #3f69cc !important;
    }
}

md-checkbox.md-checked {
    [class^="icon-"], [class*=" icon-"] {
        background-color: #6198ff !important;
    }
}

md-checkbox.md-default-theme.md-checked.md-focused .md-container:before, md-checkbox.md-checked.md-focused .md-container:before {
    background-color: rgba(63, 105, 204, .3) !important;
}

table .md-table {
    border-spacing: 0px 8px;
    border-collapse: collapse;

    tr {
        background-color: white;
    }
}

.mini-avatar {
    border-radius: 100%;
}

md-autocomplete {
    background: none !important;
}

.tag-dropdown-popup, .user-dropdown-popup {
    position: absolute;
    z-index: 10;
    display: none;
    background-color: #fff;
    box-shadow: 6px 6px 8px 0 rgb(0, 0, 0, .15);
    width: 200px;
    max-height: 170px;
    overflow-x: auto;
    margin-top: 45px;
    border: 1px solid rgba(0, 0, 0, .1);

    &.open {
        display: block !important;
    }

    ul {
        li {
            cursor: pointer;
            padding: 10px 15px;

            &:hover {
                background-color: #f3f6f8;
            }
        }
    }
}

.tag-input-wrapper {
    .tag-input {
        width: 100%;
    }

    .plus-icon {
        margin-left: 2px;
        height: 16px;
        width: 16px;
    }

    .add-tag {
        cursor: pointer;
        display: inline-block;
        padding: 4px 2px;
    }

    .tag-item {
        background: #ddebff;
        border-radius: 5px;
        color: #2b78e4;
        cursor: pointer;
        display: inline-block;
        margin: 0 4px 5px 0;
        padding: 4px 8px;

        &.shopify {
            background-color: green;
            color: white;
            cursor: default;
        }
    }
}

.compose-email-content {
    width: 1000px;
    height: 350px;
}
