.table {
    border-collapse: collapse;
    border-spacing: 0;
    width: 99%;
    //margin: 10px auto;

    table {
        border-spacing: 0;
    }

    md-checkbox {
        margin: 0;
    }

    button {
        margin: 0 !important;
        padding: 0 !important;
        width: auto !important;
        height: auto !important;
    }

    thead {
        tr {
            height: 50px;
        }

        th {
            padding: 15px;
            font-weight: 500;
            text-align: left;
            min-height: 50px;
        }
    }

    tbody {
        tr {
            transition: .2s;

            &:hover {
                background-color: #eee;
            }
        }

        td {
            padding: 10px 15px;
            min-height: 50px;
            max-width: 100px;

            .md-button {
                min-width: auto;
            }

            .md-icon-button {
                height: auto;
                width: auto;
                padding: 0;
                margin: 0;
            }
        }
    }
}

table.md-table td.md-cell {
    border-top: none;
}

table {
    .md-column {
        font-size: 12px;
    }
}

.large-table {
    thead {
        th {
            padding: 0 15px !important;

            &.more-columns {
                padding: 0 !important;
            }
        }
    }

    td {
        height: 55px;
    }
}

