@import 'mixin';

.tse-scrollable .tse-scrollbar {
    z-index: -1;

    .drag-handle {
        width: 0; // hidden scrollbar custom
    }
}

.setting-form {
    margin: 15px 0 0;

    .form-row {
        margin-right: -7.5px;
        margin-left: -7.5px;
        align-items: center;
        display: flex;
        flex-wrap: wrap;
    }

    .form-control {
        input {
            &:disabled {
                background-color: #e8e9ea;
                transition-duration: 200ms;
                transition-property: background-color;
                cursor: not-allowed;
            }
        }

        md-input-container {
            button {
                padding: 0 15px 15px 15px;
            }
        }

        textarea {
            padding: 15px 20px;
        }

        .md-input-messages-animation {
            //position: absolute;
            //bottom: -8px;
        }
    }

    .social-accounts {
        margin-right: 10px;
    }

    .form-group {
        margin-bottom: 10px;
        flex-grow: 1;
        position: relative;

        &.last {
            margin-bottom: 0;
        }

        label {
            color: #656a72;
            font-size: 16px;
            font-weight: 600;
            line-height: 1.7;
            margin-bottom: 2px;
            display: inline-block;
        }
    }

    .col-sm-6 {
        box-sizing: border-box;
        padding-right: 7.5px;
        padding-left: 7.5px;
        flex: 0 0 50%;
        max-width: 50%;
        position: relative;
        width: 100%;
    }
}

.side-menu {
    background-color: $MainMenuBlueColor;
    height: 100%;
    max-height: 100%;
    text-align: center;
    width: 110px;

    .scrollable-content {
        margin-right: 12px;
    }

    .menu-item {
        display: flex;
        margin-bottom: 30px;
        position: relative;

        &:first-child {
            display: block;
            height: 62px;
            padding-top: 12px;
            border-bottom: 1px solid #3436C5;

            div, svg {
                opacity: 1;
            }

            svg {
                width: 36px;
                height: 33px;
            }

            .nav-item:after {
                display: none;
            }
        }

        &:last-child {
            margin-bottom: 0;
        }

        div {
            font-size: 12px;
            color: #fff;
        }
    }

    .nav-item {
        padding: 10px 0;
        width: 100%;
        text-transform: capitalize;

        &.active::after {
            content: ' ';
            position: absolute;
            width: 14px;
            height: 14px;
            top: 20px;
            right: -12px;
            background-color: $MainMenuBlueColor;
            border: solid 5px #fff;
            border-radius: 50%;
            z-index: 1;
        }

        .menu-icon {
            width: 24px;
            height: 24px;
            fill: #fff;
            margin-bottom: 10px;
        }

        div,
        svg {
            opacity: 0.7;
        }

        &:hover,
        &.active {
            div,
            svg {
                opacity: 1;
            }
        }

        .badge {
            background: #ff9900;
            border-radius: 50%;
            height: 6px;
            position: absolute;
            right: 29px;
            top: 9px;
            width: 6px;
        }
    }
}

.apps-popup {
    background-color: #fff;
    box-shadow: 1px 1px 5px rgba(0, 0, 0, .15);
    font-size: 14px;
    height: auto;
    margin-top: 5px;
    position: fixed;
    right: 64px;
    transition: all .5s;
    width: 250px;
    z-index: 1000;

    @keyframes apps-popup-forward {
        0% {
            transform: scale(0);
            transform-origin: 20% 80%;
        }
        75% {
            transform: scale(1.1);
            transform-origin: 20% 80%;
        }
        100% {
            transform: scale(1);
            transform-origin: 20% 80%;
        }
    }

    @keyframes apps-popup-backward {
        0% {
            transform: scale(1);
            transform-origin: 20% 80%;
        }
        75% {
            transform: scale(1.1);
            transform-origin: 20% 80%;
        }
        100% {
            transform: scale(0);
            transform-origin: 20% 80%;
        }
    }

    &.user {
        top: 60px;
        right: 20px;
        margin: 0;
        padding: 15px;
        position: absolute;
        width: 235px;
        border-radius: 10px;
    }

    &.chat {
        border-radius: 5px;
        position: absolute;
        padding: 10px;
        left: 25px;
        bottom: 20px;
    }

    &.guest-popup {
        top: 45px;
        left: 0;
        position: absolute;
    }

    &.group-chat {
        left: 50px;
        top: 58px;
    }

    &.assign {
        border-radius: 5px;
        padding: 10px;
        position: absolute;
        right: 10px;
        left: auto;
        top: 40px;

        h4 {
            color: #231f20;
            text-align: center;
        }
    }

    &.reply {
        width: 330px;

        .search-input-chat {
            box-sizing: border-box;
            padding: 0 10px;
        }

        h4 {
            color: #231f20;
            text-align: center;
        }

        .popup-list-item {
            padding: 5px 10px 4px;
            border-radius: 5px;
            margin: 0 !important;

            &:hover {
                background: #dfebfd;
            }
        }
    }

    .popup-list-item {
        cursor: pointer;
        padding: 5px 10px 4px;
        border-radius: 5px;
        margin: 0 !important;

        &:hover {
            background: #dfebfd;
        }

        &:last-child {
            margin-bottom: 0;
        }

        img {
            margin-right: 7px;
            vertical-align: middle;
        }

        .md-button {
            padding: 0 !important;

            &:hover {
                background: none !important;
            }
        }
    }

    h4 {
        font-weight: 500;
        margin-bottom: 10px;
    }

    .avatar-block {
        position: relative;
        margin-right: 20px;

        img {
            margin: 0;
        }
    }

    .title-toggle {
        border-bottom: 1px solid;
        color: #286efa;
        cursor: pointer;
    }

    .md-list-item-inner {
        overflow: hidden;
        padding-left: 0;
        white-space: nowrap;
        width: 90%;
    }

    .shortcut, .text-reply {
        display: inline-block;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }

    .text-reply {
        width: 65%;
    }

    .link-status {
        color: #0095cf;
        cursor: pointer;
    }

    .shortcut {
        padding-right: 4%;
        width: 30%;
    }

    .application-loading-wrapper {
        top: auto;
        margin-top: 0;
    }

    .close-icon-chat {
        position: absolute;
        right: 0;
        top: 2px;
    }

    .search-input-chat {
        margin-bottom: 10px !important;

        .md-input {
            border: 1px solid #eee;
            border-radius: 6px;
            background: rgba(0, 0, 0, .03);
            padding: 0 10px;
        }
    }

    h3 {
        cursor: default;
        font-size: 15px;
        margin: 0 0 5px;
        text-align: center;
    }

    md-menu-content {
        padding: 0;
    }

    .arrow-up {
        height: 0;
        border: 10px solid transparent;
        border-right: 8px solid #fff;
        position: absolute;
        bottom: 12px;
        left: -17px;
        width: 0;
    }

    .app-item {
        width: 105px;
        height: auto;
        padding: 10px;
        text-align: center;
        float: left;
        display: inline-block;

        a {
            display: block;
            padding: 25px 5px 5px;
            border-radius: 5px;
            text-decoration: none;
            color: #8a99aa;

            .icon-20 {
                margin-top: 20px;
                padding: 10px;
                border-radius: 100%;
            }

            &:hover {
                background: grey;
            }

            p {
                border-bottom: 0;
            }
        }

        &.core-menu {
            .icon-20 {
                color: #3f69cc !important;
                background-color: #cfe0ff !important;
            }
        }

        &.task-menu {
            .icon-20 {
                color: #017872 !important;
                background-color: #bdf7bd;
            }
        }

        &.livechat-menu {
            .icon-20 {
                color: #5c54ff !important;
                background-color: #dedcff;
            }
        }

        &.inbox-menu {
            .icon-20 {
                color: #0095cf !important;
                background-color: #cce9f5;
            }
        }
    }

    &.close {
        transform: scale(0);
        animation-name: apps-popup-backward;
        opacity: 0;
        transition: opacity 5s ease;
        animation-name: apps-popup-backward;
        animation-duration: 300ms;
        animation-iteration-count: 1;
    }

    &.open {
        display: block;
        z-index: 2;
        opacity: 1;
        transform: scale(1);
        animation-duration: 300ms;
        animation-iteration-count: 1;
        transition: opacity .1s linear;
        animation-name: apps-popup-forward;
    }
}

.md-open-menu-container {
    .user-status-icon {
        border: 1px solid #fff;
        border-radius: 50%;
        height: 10px;
        margin-right: 5px;
        display: inline-block;
        width: 10px;

        &.online {
            background: green;
        }

        &.away {
            background: #FFBF00;
        }

        &.offline {
            background: #cacaca;
        }
    }
}

.user-status {
    position: absolute;
    width: 10px;
    border-radius: 50%;
    top: 28px;
    right: 0;
    border: 1px solid #fff;
    height: 10px;

    &.online {
        background: green;
    }

    &.away {
        background: #FFBF00;
    }

    &.offline {
        background: #cacaca;
    }
}

.mail-submenu {
