.article-index {
	flex-grow: 1;
	display: flex;
	flex-direction: column;
	align-items: center;

	.article-title {
		height: 52px;
		width: 100%;
		box-shadow: rgb(221 226 230) 0px -1px 0px inset;
		display: flex;
		justify-content: flex-end;
		padding: 10px 20px;
		position: sticky;
		top: 0;
		background: #fff;
		z-index: 100;
	}

	.md-input {
		border: none !important;
		border-bottom: 1px solid #eee !important;
	}

	.title-field {
		margin-bottom: 15px;

		.md-input {
			font-size: 20px;
		}
	}

	.description-field {
		margin-bottom: 15px;

		.md-input {
			font-size: 16px;
		}
	}

	.article-content {
		max-width: 100%;
		flex-grow: 1;
		display: flex;
		width: 100%;

		.action-text-editor {
			background-color: #fff;
			border-radius: 5px 5px 0 0;
			border-bottom: 1px solid #dadada;
			padding: 7px 0;
			margin: 20px 0 10px;

			.md-button {
				border-radius: 5px;
				cursor: pointer;
				height: 30px;
				margin: 0 5px;
				line-height: normal;
				min-width: auto;
				width: 30px;

				&.md-focused {
					background-color: transparent;
				}

				&.active, &:hover {
					background-color: #f0f0f0 !important;
				}
			}
		}
	}

	.actions-block {
		display: flex;
		flex-shrink: 0;
		justify-content: flex-end;
		align-items: center;
		min-width: 250px;

		.switch-wrap {
			display: flex;
			align-items: center;

			span {
				color: #989a9f;
				text-transform: uppercase;

				&.published {
					color: green;
				}
			}
		}

		.paper-switch {
			margin-left: 5px !important;
		}

		.md-button {
			margin: 0 10px;
		}
	}

	.editor-column {
		flex: 1;
		padding: 20px;
		display: flex;
		flex-direction: column;

		.ql-container {
			flex: 1;
			margin-bottom: 10px;
		}

		md-input-container {
			flex: 1;
		}
	}

	.editor-sidebar {
		width: 400px;
		flex-shrink: 0;
		background: rgb(246, 246, 247);
		padding: 24px 20px;

		.sidebar-inner {
			display: flex;
			flex-direction: column;
			position: sticky;
			top: 78px;

			.card {
				background-color: #fff;
				border: 2px solid #f5f5f5;
				border-radius: 5px;
				padding: 5px 10px;
				margin-bottom: 10px;
			}
		}

		.cp-Panel-toggle {
			color: #5d5e64;
			display: flex;
			justify-content: space-between;
			font-weight: 500;
			padding: 10px 0;

			.arrow-icon {
				transition: .2s;
				transform: rotate(90deg);
				height: 16px;
				width: 16px;
			}

			&.cp-is-open {
				.arrow-icon {
					transform: rotate(0deg);
				}
			}
		}
	}
}

.article-wrap {
	flex: 1;

	.article-title {
		display: flex;
		justify-content: space-between;
		padding: 10px;

		h2 {
			margin-right: 10px;
			text-transform: uppercase;
		}
	}

	.buttons-panel {
		justify-content: flex-end;
	}

	.search-block {
		align-items: center;
		display: flex;
		justify-content: space-between;
		position: relative;
	}

	.search-icon {
		height: 16px;
		position: absolute;
		width: 16px;
		z-index: 1;
		left: 7px;
	}

	.search-input-chat {
		padding: 0;

		.md-input {
			border-radius: 5px;
			border: 1px solid #eeecec;
			padding: 0 5px 0 27px;
			color: #989a9f;
			font-weight: 400;
			height: 33px;
			font-size: 12px;
		}

		.md-input::placeholder {
			color: #989a9f;
		}
	}

	table {
		.ember-power-select-trigger {
			border: none !important;
			background: transparent !important;
			padding: 7px 0 !important;
		}

		.status {
			color: #2B78E4;

			&.DRAFT {
				color: #989a9f;
			}
		}

		.ratings {
			display: flex;

			.svg-icon {
				margin-right: 6px;
				height: 16px;
				width: 16px;
				fill: #219653;
			}

			.like, .dislike, .rating {
				width: 30px;
			}

			.dislike-icon {
				fill: #e24b4b;
				transform: rotateX(180deg);
			}

			.rating {
				color: #219653;
			}
		}

		.eye-icon {
			height: 16px;
			width: 16px;
			margin-right: 5px;
		}
	}


	.actions-block {
		display: flex;

		.ember-power-select-trigger {
			min-width: 200px;
		}

		.primary-button {
			margin-left: 10px;
		}
	}

	.content-block {
		margin-right: 30px;

		img {
			height: 50px;
			width: 50px;
		}

		video {
			object-fit: fill;
			height: 70px;
			width: 150px;
		}
	}
}

.kb-menu {
	border-right: 1px solid #dadada;
	padding: 10px;
	color: #121416;
	width: 240px;

	.md-button {
		font-weight: 500;
		font-size: 15px;
		display: flex;
		justify-content: space-between;
		margin-bottom: 15px;
		width: 100%;

		&:hover {
			background: none !important;
		}
	}

	.kb-item {
		margin-bottom: 12px;
		display: flex;
		justify-content: space-between;
		padding: 10px;
		color: #2b78e4;
		background-color: #eee;
		font-weight: 500;
	}
}

.preview-article {
	margin-left: 85px;
	min-height: 600px;
	width: 100%;

	md-toolbar {
		min-height: auto;
	}

	.md-toolbar-tools {
		display: block;
		max-height: none;
		height: auto;
		padding: 10px 20px;
	}

	.md-dialog-content {
		background-color: #f7f9fa;
		flex: 1;
	}

	.wrap-row {
		display: flex;
		flex-wrap: wrap;
	}

	.list-articles {
		margin-top: 10px;
		font-size: 14px;

		> div {
			margin-bottom: 5px;
		}
	}

	.bread-crumbs {
		margin-bottom: 10px;
	}

	.kb-item {
		cursor: pointer;
		margin: 20px;
		width: 200px;
		min-height: 80px;
		border: 2px solid #dadada;
		border-radius: 5px;
		padding: 20px;
	}

	.category-aside {
		width: 250px;

		.search-input {
			margin: 0 0 20px;
			width: 80%;
		}

		h2 {
			color: #121416;
			margin-bottom: 20px;
		}

		.categories {
			> div {
				color: #121416;
				font-size: 15px;
				margin-bottom: 10px;

				&:hover span {
					border-color: #121416;
				}

				span {
					border-bottom: 1px solid transparent;
					cursor: pointer;
					display: inline-block;
				}
			}
		}
	}

	.content-aside {
		background-color: #fff;
		box-shadow: rgb(0 0 0 / 5%) 0 0 0 1px, rgb(241 243 245) 0 5px 20px 0, rgb(0 0 0 / 5%) 0 3px 3px 0;
		border-radius: 5px;
		flex: 1;
		height: 100%;
		padding: 20px;

		.search-item {
			border: 2px solid #dadada;
			border-radius: 5px;
			padding: 10px;
			margin-bottom: 10px;

			.title {
				font-size: 16px;
				font-weight: 600;
				margin-bottom: 5px;
			}

			.author {
				margin-bottom: 10px;
				font-size: 14px;
			}

			.publish {
				color: #2B78E4;
			}
		}

		h4 {
			margin-bottom: 10px;
		}
	}

	.search-input {
		margin-top: 10px;
	}

	.title {
		font-size: 24px;
		font-weight: 500;
		color: #121416;
		margin-bottom: 25px;
	}

	.ql-editor {
		padding: 0;
	}

	.rating-wrap, .tags-wrap {
		border: 2px solid #dadada;
		border-radius: 5px;
		text-align: center;
		padding: 10px;
		margin-top: 15px;

		a {
			display: inline-block;
			margin-left: 2px;
		}

		.title-rating {
			font-size: 16px;
			color: #121416;
			font-weight: 500;
			margin-bottom: 10px;
		}

		.icons-rating {
			margin-bottom: 10px;

			.svg-icon {
				margin: 0 15px;
				height: 24px;
				width: 24px;
				fill: #219653;

				&.dislike {
					fill: #e24b4b;
					transform: rotateX(180deg);
				}
			}
		}
	}

	.tags-wrap {
		text-align: left;
	}
}
