// custom styles for Tooltip
#paper-wormhole {
  .md-panel.md-tooltip {
    background-color: #fff;
    border-radius: 5px;
    border: 1px solid #eeecec;
    box-shadow: 0 10px 20px 0 rgba(0, 0, 0, .15);
    color: #5d5e64;
    font-size: 11px;
    font-weight: 400;
    height: auto !important;
    line-height: 13px;
    margin-top: 5px !important;
    opacity: 1;
    padding: 10px 12px;
  }
}

md-sidenav {
  width: calc(100% - 100px);
  min-width: calc(100% - 100px);
  max-width: calc(100% - 100px);
}

.md-sidenav-backdrop {
    backdrop-filter: blur(0px);
    transition-duration: 200ms;
    opacity: 0;
    transition-property: backdrop-filter, opacity;
    transition-timing-function: linear;
    &.md-opaque {
        opacity: 1 !important;
        transition: 200ms opacity linear;
        backdrop-filter: blur(10px);
        background-color: transparent !important;
    }
}

.date-picker {
  border: 0;
  border-bottom: 1px solid rgba(0, 0, 0, 0.12);
  padding: 5px 0;
  background: #fff;
  border-radius: 3px;
}

.date-picker-input {
  border: 0;
  border-bottom: 1px solid rgba(0, 0, 0, 0.12);
  padding: 5px;
  width: 100%;
  margin-top: 10px;
  font-size: 14px;
}

.file-upload-drag-drop {
  border: 2px dashed rgba(0, 0, 0, 0.12);
  border-radius: 10px;
  min-height: auto;
  .dz-message {
    margin: 10px;
  }
}

.simditor{
  margin-bottom: 20px;
  border-radius: 6px;
  border: none;
  padding: 10px;
  .simditor-wrapper {
      background: none !important;
  }
  .simditor-body{
    height: 100%!important;
    min-height: auto!important;
  }
  .simditor-placeholder,.simditor-body{
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif !important;
    padding: 10px !important;
    font-size: 14px !important;
    overflow-y: auto;
  }
  .simditor-toolbar{
    z-index: 1 !important;
    border-bottom: none !important;
    background-color: #f5f5f5;
  }
}

.cp-Panel {
  width: 100%;
  position: relative;

  .liquid-container {
    width: 100%;
  }
  .cp-Panel-toggle {
    display: block;
    text-align: left;
  }
  .toggle-button {
    position: absolute;
    float: right;
    margin: 5px;
    right: 0;
    transition: .3s;
  }

  .down-arrow {
    display: inline-block;
    margin: 0 2px 4px 0;
    vertical-align: middle;
    transition: .3s;
  }

  .cp-is-open {
    .toggle-button, .down-arrow {
      transform: rotate(180deg);
    }
  }
}

md-menu-item {
  font-size: 14px;
  .md-button {
    font-size: 14px;
  }
  .md-ripple-container {
      transition: none;
  }
}

.table {
  .md-button.md-raised {
    background: none !important;
  }
}

.password-wrapper {
    position: relative;

    button {
        margin: 4px !important;
        z-index: 1;
        padding: 5px 0 0 0 !important;
    }
    .visibilityButton {
        right: 0;
        position: absolute;
        margin: 0!important;
        top: 0;
        padding: 0!important;
    }
    input {
        box-sizing: border-box;
    }
}

md-toolbar {
  //max-height: 50px;
  //min-height: 50px;
  //height: 50px;
}

.ember-power-select-search {
  background: #fff;
}

md-list-item {
  min-height: auto !important;
  &:before {
    display: none !important;
  }
  .md-list-item-inner {
    min-height: auto !important;
    &:before {
      display: none !important;
    }
  }
}

md-option {
  font-size: 14px;
  height: 40px;
}

.md-toolbar-tools{
  font-size: 16px;
}

md-backdrop{
  position: fixed;
  z-index: 1 !important;
}
md-card{
  box-shadow: none;

  &.transparent{
    background-color: transparent !important;
  }
}

table.md-table{
  thead{
    .md-row{
      height: 40px !important;
      button{
        border-radius: 4px;
      }
    }
  }
  th.md-column{
    color: #90a4ae;
    font-weight: 600;
    padding: 0;
    &.md-active{
      color: #90a4ae;
    }
    md-icon {
      &.md-sort-icon{
        color: #90a4ae!important;
        opacity: 0 !important;
      }
    }
  }
}

.dz-preview{
  z-index: 1 !important;
  &:hover{
    .dz-image{
      img{
        -webkit-filter: none !important;
        filter: none !important;
      }
    }
    .dz-details{
      display: none;
    }
  }
}

.dz-image{
  border-radius: 10px !important;
  z-index: 1 !important;
}

.dz-success-mark{
  opacity: 1 !important;
  svg{
    p{
      fill: #017872;
    }
  }
}

.active{
  .icon{
    color: #6198ff;
  }
}

md-toast{
  position: fixed !important;
}

.date-picker-input:read-only{
  background-color: #ffff;
  border-bottom-style: dotted !important;
  color: #656a72 !important;
}

md-tabs-wrapper{
  border:none !important;
  md-ink-bar{
    height: 3px;
    display: none;
  }
}

md-tabs {
  .md-tab {
    text-transform: capitalize;
    color: #989a9f;

    &.md-focused {
      background: none;
    }

    .md-ripple-container {
      display: none;
    }
  }

  .md-active {
    border-bottom: 2px solid #2b78e4;
    color: #2b78e4;
  }
}

md-checkbox{
  &.md-checked{
    .md-icon{
      background-color: #3f69cc !important;
    }
  }
}
