code {
    font-family: monospace;
    font-size: 12px;
}

.dropdown-popup {
    position: absolute;
    transform: scale(0);
    width: 210px;
    opacity: 0;
    transition: opacity 3s ease;
    padding: 0;
    background: #fff;
    border: 1px solid #eee;
    color: currentColor;
    border-radius: 5px;
    box-shadow: 1px 1px 4px rgba(0, 0, 0, .1);
    margin-left: -220px;
    animation-name: popup-backward;
    animation-duration: 300ms;
    animation-iteration-count: 1;
    z-index: 10;

    @keyframes popup-forward {
        0% {
            transform: scale(0);
            transform-origin: 80% 60%;
        }
        75% {
            transform: scale(1.2);
            transform-origin: 80% 60%;
        }
        100% {
            transform: scale(1);
            transform-origin: 80% 60%;
        }
    }

    @keyframes popup-backward {
        0% {
            transform: scale(1);
            transform-origin: 80% 60%;
        }
        75% {
            transform: scale(1.2);
            transform-origin: 80% 60%;
        }
        100% {
            transform: scale(0);
            transform-origin: 80% 60%;
        }
    }
    &.open {
        transform: scale(1);
        animation-name: popup-forward;
        animation-duration: 300ms;
        animation-iteration-count: 1;
        width: 210px;
        opacity: 1;
        transition: opacity .1s linear;
    }

    &.fixed {
        right: 20px;
    }
    &.one-option {
        margin-top: -35px;
    }
    &.two-options {
        margin-top: -65px;
    }
    &.three-options {
        margin-top: -110px;
    }

    a {
        cursor: pointer;
        display: block;
        line-height: 30px;
        padding: 0;
        font-size: 14px;
        font-weight: 500;
        color: currentColor;
        margin: 0;
        text-transform: capitalize;
        float: left;
        width: 100%;

        &:hover {
            background-color: rgba(158, 158, 158, .2);
        }
    }
    md-icon {
        color: #8393a2
    }
    span {
        font-weight: 500;
    }

    button {
        width: 100% !important;
        padding: 5px 15px !important;
        text-align: left;
        display: block;
        color: currentColor;
    }
}

.custom-drop-down-button {
    color: #656a72;

    md-icon {
        color: #656a72 !important;
    }
}

.user-profile {
    border-right: 1px solid #dadada;
    width: 240px;

    .profile-title {
        color: #231f20;
        font-weight: 500;
        font-size: 16px;
        padding: 15px 20px 20px;;
    }

    .profile-menu {
        padding: 0 6px;
        margin-top: 10px;

        a {
            display: flex;
            background-color: #effaff;
            border-radius: 5px;
            padding: 6px 14px;
        }
    }

    .icon-bell {
        height: 16px;
        width: 16px;
        margin-right: 8px;
        fill: #2B78E4;
    }

    .label {
        color: #2B78E4;
        font-weight: 500;
        font-size: 14px;
    }
}

.notification-form {
    padding: 20px;

    md-radio-button {
        display: inline-block;
        margin-right: 25px;
    }

    .notification-form-group {
        margin-bottom: 15px;
    }

    .notification-warn {
        border-radius: 4px;
        background-color: rgba(224, 30, 90, .1);
        border: 1px solid rgba(224, 30, 90, .4);
        padding: 12px 16px;
        display: inline-block;

        .notification-warn_icon {
            color: #e01e5a;
            margin-right: 10px;
            display: inline-block;
            font-size: 18px;
            vertical-align: middle;
        }
    }

    .paper-switch, .md-checkbox {
        margin: 0;
    }

    .notification-form_icon {
        height: 24px;
        width: 24px;
        margin-right: 10px;
    }

    .notification-form_title {
        margin-left: 34px;

        .label {
            color: #5d5e64;
            font-weight: 500;
            margin-right: 10px;
        }

        .snooze-title {
            margin: 5px 10px 10px 0;
        }

        .label-field {
            margin-bottom: 5px;
        }

        .flatpickr-input {
            background-color: transparent;
            padding: 9px 10px;
            position: relative;
            width: 100%;
            z-index: 1;
        }

        .date-wrap-field {
            width: 245px;
            margin-bottom: 12px;
            position: relative;

            .calendar-icon {
                height: 16px;
                width: 16px;
                position: absolute;
                top: 29px;
                right: 10px;
                z-index: 0;
            }
        }

        .checkbox-field {
            margin-top: 12px;

            .md-label {
                margin-top: 3px;
            }
        }

        .ember-power-select-trigger {
            width: 245px;
        }
    }
}
